<template>
  <v-container grid-list-md text-xs-center>
    <v-layout row wrap>
      <v-flex xs12>
        <v-layout row wrap>
          <v-flex xs6>
            <h1 class="text-sm-left"><v-icon large>list</v-icon> LIST</h1>
          </v-flex>
          <v-flex xs6>
            <div class="text-sm-right">
              <!-- <v-btn  slot="activator" color="grey darken-2" dark round class="mb-2" to="/TEACHER/write">등록</v-btn> -->
              <v-tooltip bottom>
                <v-btn slot="activator" dark icon to="/TEACHER/write"><v-icon>edit</v-icon></v-btn> 
                <span>등록</span>
              </v-tooltip>
            </div>
          </v-flex>
        </v-layout>

        <v-divider></v-divider>

        <v-layout row wrap>
          <v-flex xs8></v-flex>
          <v-flex xs4>
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-flex>
        </v-layout>

        <v-data-table
          :headers="headers"
          :items="desserts"
          :search="search"
          :pagination.sync="pagination"
          hide-actions
          class="elevation-1"
        >
          <template slot="headerCell" slot-scope="props">
            <v-tooltip bottom>
                <span slot="activator">
                {{ props.header.text }}
                </span>
                <span>
                {{ props.header.text }}
                </span>
            </v-tooltip>
          </template>
          <template slot="items" slot-scope="props">
            <td>{{ props.item.userId }}</td>
            <td class="text-xs-center">{{ props.item.userNm }}</td>
            <td class="text-xs-center">{{ props.item.userAge }}</td>
            <td class="text-xs-center">{{ props.item.userTel }}</td>
            <td class="text-xs-center">{{ props.item.userSt }}</td>
            <td class="text-xs-center">{{ props.item.insertDt }}</td>
            <td class="text-xs-center">
              <router-link :to="{ name: 'TeacherInfo', params: { userId: 123 }}">
                <v-icon color="white">search</v-icon>
              </router-link>
            </td>
          </template>
        </v-data-table>
        <div class="text-xs-center pt-2">
          <v-pagination v-model="pagination.page" :length="pages" :circle=true></v-pagination>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>

export default {
  name: 'Teacher_List',
  data () {
    return {
      dialog : false,        
      search: '',
      pagination: {},
      selected: [],
      headers: [
        { text: '사용자 ID', value: 'userId', align: 'center', sortable: true},
        { text: '이름', value: 'userNm', align: 'center', sortable: false},
        { text: '나이', value: 'userAge', align: 'center', sortable: false },
        { text: '전화번호', value: 'userTel', align: 'center', sortable: false },
        { text: '상태', value: 'userSt', align: 'center', sortable: false },
        { text: '등록일', value: 'insertDt', align: 'center', sortable: false },
        { text: 'view', value: 'view', align: 'center', sortable: false }
      ],
      desserts: [
        // {
        //   value: false,
        //   userId: 'Frozen Yogurt',
        //   userNm: 159,
        //   userAge: 6.0,
        //   userTel: 24,
        //   userSt: 4.0,
        //   insertDt: '1%'
        // },
        // {
        //   value: false,
        //   userId: 'Ice cream sandwich',
        //   userNm: 237,
        //   userAge: 9.0,
        //   userTel: 37,
        //   userSt: 4.3,
        //   insertDt: '1%'
        // },
        // {
        //   value: false,
        //   userId: 'Eclair',
        //   userNm: 262,
        //   userAge: 16.0,
        //   userTel: 23,
        //   userSt: 6.0,
        //   insertDt: '7%'
        // },
        // {
        //   value: false,
        //   userId: 'Cupcake',
        //   userNm: 305,
        //   userAge: 3.7,
        //   userTel: 67,
        //   userSt: 4.3,
        //   insertDt: '8%'
        // },
        // {
        //   value: false,
        //   userId: 'Gingerbread',
        //   userNm: 356,
        //   userAge: 16.0,
        //   userTel: 49,
        //   userSt: 3.9,
        //   insertDt: '16%'
        // },
        // {
        //   value: false,
        //   userId: 'Jelly bean',
        //   userNm: 375,
        //   userAge: 0.0,
        //   userTel: 94,
        //   userSt: 0.0,
        //   insertDt: '0%'
        // },
        // {
        //   value: false,
        //   userId: 'Lollipop',
        //   userNm: 392,
        //   userAge: 0.2,
        //   userTel: 98,
        //   userSt: 0,
        //   insertDt: '2%'
        // }
      ]
    }
  },
  created : function() {
    this.getUserList();
  },
  methods: {
    getUserList : function() {
      const baseURI = '/user/getAxiosListUser';
      this.$http.get(`${baseURI}`).then((result) => {
        this.desserts = result.data.content
      })
    }
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) return 0

      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fl {
    float : left;
}
.fr {
    float : right;
}
a {
  text-decoration: none;
}
</style>



